.navbar {
	position: sticky;
	height: 8vh;
	display: flex;
	justify-content: start;
	align-items: center;
	z-index: 1000;
	width: 100vw;
	top: 0px;
	left: 0px;
	right: 0px;
	transition: all 600ms 450ms;
	background: rgba(28, 26, 44, 0.85);
	box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
	backdrop-filter: blur(6px);
	-webkit-backdrop-filter: blur(6px);
	border-radius: 0px;
	border: 0px solid rgba(255, 255, 255, 0.18);
}
.navbar.blackmode {
	background-color: black;
	transition: all 100ms;
	z-index: 600;
}
.logo {
	position: absolute;
	color: black;
	right: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row-reverse;
	transition: all 300ms;
}
.logo.hiddenLogo {
	opacity: 0;
	pointer-events: none;
}
.logo h1 {
	color: #d45d44;
	font-family: 'Comfortaa', cursive;
	font-weight: 800;
}
@media (min-width: 520px) {
}
.logo-icon {
	width: 100px;
	height: 100px;
	z-index: 100;
}
#MalteBantchev {
	position: absolute;
	color: white;
	right: 15px;
	top: 20px;
	width: 40px;
}
.menu-bars1 {
	margin-left: 2rem;
	margin-top: -0.3rem;
	font-size: 2rem;
	background: none;
	z-index: 10;
	transition-property: all;
	transition-duration: 0.2s;
	transition-delay: 300ms;
}

.menu-bars {
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
	z-index: 10;
	transition-property: all;
	transition-duration: 1s;
}

.nav-menu {
	background-color: rgb(0, 0, 0);
	width: 100vw;
	position: fixed;
	top: 0vh;
	padding-top: 0px;
	margin: 0px;
	left: 0%;
	right: 0%;
	transition: all 500ms ease-out;
	z-index: 10;
	background: rgba(28, 26, 44, 0.9);
	box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
	backdrop-filter: blur(20px);
	-webkit-backdrop-filter: blur(20px);
	border-bottom: 1px solid rgba(128, 128, 128, 0.055);
	clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
}

.nav-menu.active {
	left: 0;
	right: 0;
	z-index: 100;
	clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

.nav-text {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 8px 0px 8px 0px;
	list-style: none;
	display: flex;
	flex-direction: column;
	text-align: center;
	width: 100%;
	z-index: 100;
	color: red;
	transition: all 700ms ease-out;
	opacity: 0;
	margin: auto;
}
.nav-content {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 8px 0px 8px 0px;
	list-style: none;
	display: flex;
	flex-direction: column;
	text-align: center;
	width: 80%;
	margin: auto;
	margin-top: 7vh;
	z-index: 100;
	color: red;
	transition: all 700ms 200ms;
	opacity: 0;
	text-decoration: none;
	transform: scaleY(1);
	transform-origin: top;
}
.nav-content > p {
	text-decoration: none;
	color: white;
	opacity: 0;
	background: white;
}
.nav-content.clickedBurger {
	transform: scaleY(1);
	transition: all 0ms 0ms ease-out;
	opacity: 100;

	text-decoration: none;
	text-decoration: none;
	color: white;
}

.nav-text a:hover > Link {
	text-shadow: 0px 10px 10px 10px rgba(255, 255, 255, 0.2);
	z-index: 10;
}
