.divider-nav {
	margin: 0px 0px;
	padding: 1px;
	color: transparent;
	z-index: 10;
	width: 90%;
	height: 1px;
	max-width: 300px;
	border-radius: 10px;
	background: transparent;
}
.divider-navWIDE {
	margin: 0px 0px;
	padding: 1px;
	color: transparent;
	z-index: 10;
	width: 100vw;
	height: 3px;
	border-radius: 10px;
	background: #242424;
}
.nav-menu-items {
	width: 100%;
	z-index: 10;
	margin: 20px;
}

.navbar-toggle {
	display: none;
}

span {
	margin-left: 16px;
	z-index: 10;
}

#Navbar-Text {
	font-weight: 900;
	margin-left: 8px;
	font-family: 'Roboto Condensed', sans-serif;
	color: rgb(0, 0, 0);
}

.burger {
	width: 40px;
	width: 40px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin-left: 1.4vh;
	z-index: 100;
}

.burger-line {
	width: 80%;
	height: 5px;
	background: #d45d44;
	border-radius: 10px;
	margin: 2px;
	transition: margin 200ms 0ms ease-out, width 200ms 150ms ease-out,
		transform 400ms 0ms ease-out, background 1000ms;
}
.burger-line.middle-line {
	width: 0%;
	background: #df5213;
	transition: width 500ms;
}
.burger-line.top-line {
	margin: -7px;
	background: #df5213;
	transform: rotateZ(45deg);
}
.burger-line.bottom-line {
	margin: -7px;
	background: #df5213;
	transform: rotateZ(-45deg);
}

.LogIn {
	position: relative;
	width: 50%;
	margin-top: 0px;
	padding: 15px 10px;
	border: 3px solid #df5213;
	background: #df5213;
	color: black;
	cursor: pointer;
	display: flex;
	border-radius: 5px;
	justify-content: center;
	align-items: center;
	letter-spacing: 1px;
	max-width: 300px;
	z-index: 1000;

	margin: 10px;
	transition: all 0.3s;
}
.SignUp {
	position: relative;
	width: 50%;
	cursor: pointer;
	margin-top: 0px;
	padding: 15px 10px;
	border: 3px solid #df5213;
	color: #df5213;
	display: flex;
	border-radius: 5px;
	justify-content: center;
	align-items: center;
	letter-spacing: 1px;
	max-width: 300px;
	z-index: 1000;
	margin: 10px;
	transition: all 0.3s;
}
.SignUp:hover,
.LogIn:hover {
	cursor: pointer;
	transform: scale(1.103);
}
.text1 {
	color: #d45d44;
	text-decoration: none;
	text-decoration: none;
	font-size: 20px;
	margin: 15px 0px;
	width: 100%;
	text-align: left;
	font-weight: 600;
}
a {
	text-decoration: none;
	max-width: 400px;
	min-width: fit-content;
	margin: 0px 10px;
	white-space: nowrap;
}
.login-signup-mobile-wrapper {
	display: flex;
	cursor: pointer;
	justify-content: space-evenly;
	align-items: center;
	flex-direction: row;
	margin-top: 10px;
	position: relative;
	width: 100%;
	max-width: 400px;
}
.navbar-pc {
	position: absolute;
	overflow: hidden;
	clip: rect(0 0 0 0);
	height: 1px;
	width: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
}
@media (min-width: 1000px) {
	.logo {
		position: absolute;
		color: black;
		left: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		flex-direction: row-reverse;
		transition: all 300ms;
		font-family: 'Comfortaa', cursive;
	}
	.navbar {
		display: none;
	}
	.navbar-pc {
		height: 8vh;
		display: absolute;
		z-index: 1000;
		width: 100vw;
		position: sticky;
		top: 0px;
		left: 0px;

		transition: all 600ms 450ms;
		overflow: hidden;
		background: rgba(28, 26, 44, 0.85);
		box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
		backdrop-filter: blur(6px);
		-webkit-backdrop-filter: blur(6px);
		border-radius: 0px;
		border: 0px solid rgba(255, 255, 255, 0.18);
		display: grid;
		grid-template-columns: 3fr 2fr 3fr;
		grid-template-rows: 1fr;
		gap: 0px 0px;
		justify-content: center;
		align-items: center;
		grid-template-areas: 'logo Links text1';
	}
	.logo2 {
		position: absolute;
		color: black;
		left: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: row-reverse;
		transition: all 300ms;
		cursor: pointer;
		color: #d45d44;
		font-family: 'Comfortaa', cursive;
	}
	.login-signup-mobile-wrapper2 {
		grid-area: text1;
		cursor: pointer;
	}

	.burger-line {
		display: none;
	}
	.login-signup-mobile-wrapper2 {
		position: relative;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		flex-direction: row;
		cursor: pointer;
		right: 0px;
		position: relative;
		width: 100%;
		height: 7vh;
	}
	.LogIn2 {
		position: relative;
		width: 30%;
		margin-top: 0px;
		padding: 10px 0px;
		border: 3px solid #d45d44;
		background: #d45d44;
		color: black;
		display: flex;
		border-radius: 5px;
		justify-content: center;
		align-items: center;
		letter-spacing: 1px;
		max-width: 300px;
		z-index: 1000;
		cursor: pointer;

		margin: 5px;
		transition: all 0.3s;
		margin: 0px 10px;
		max-height: 45px;
	}
	.SignUp2 {
		position: relative;
		width: 30%;
		margin-top: 0px;
		padding: 10px 20px;
		border: 3px solid #d45d44;
		color: #d45d44;
		display: flex;
		border-radius: 5px;
		justify-content: center;
		align-items: center;
		letter-spacing: 1px;
		max-width: 300px;
		z-index: 1000;
		transition: all 0.3s;
		margin: 0px 10px;
		cursor: pointer;
		max-height: 45px;
	}
	.SignUp2:hover,
	.LogIn2:hover {
		transform: scale(1.05);
	}
	.Links {
		grid-area: Links;
		display: flex;
		justify-content: space-evenly;
		align-items: center;
	}
	.text1 {
		text-align: center;
		font-weight: 600;
		position: relative;
	}
	.text1::after {
		content: '';
		position: absolute;
		bottom: -5px;
		left: 0px;
		width: 100%;
		height: 2px;
		background: #d45d44;
		transform-origin: middle;
		transform: scaleX(0);
		transition: all 300ms;
	}
	.text1:hover::after {
		transform: scaleX(1);
		transform-origin: middle;
	}
}

.hide-everything {
	display: none;
}
