.week-navigation {
	width: 90%;
	margin: 20px auto;
	position: relative;
	display: flex;
	justify-content: space-around;
	flex-direction: row;
	align-items: center;
	border-radius: 20px;
	background: #1c1a2c;
	box-shadow: 11px 11px 22px #181625, -11px -11px 22px #201e33;
	z-index: 0;
	-webkit-tap-highlight-color: transparent;
	max-width: 500px;
	height: 50px;
}

.day-navigation {
	color: #fff;
	font-weight: 900;

	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	-webkit-tap-highlight-color: transparent;
	cursor: pointer;
}

.curser-week-navigation {
	position: absolute;
	width: 20%;
	height: 100%;
	background: rgba(255, 255, 255, 0.13);
	left: 0px;
	border-radius: 20px;
	z-index: -1;
	transition: all 0.4s;
	animation: wider 400ms ease-in-out;
	-webkit-tap-highlight-color: transparent;
}
.curser-week-navigation2 {
	position: absolute;
	width: 20%;
	height: 100%;
	background: rgba(255, 255, 255, 0.13);
	left: 0px;
	border-radius: 20px;
	z-index: -1;
	transition: all 0.4s;
	animation: wider2 400ms ease-in-out;
	-webkit-tap-highlight-color: transparent;
}
@keyframes wider {
	0% {
		transform: scaleX(1);
	}
	50% {
		transform: scaleX(1.2);
	}
	100% {
		transform: scaleX(1);
	}
}
@keyframes wider2 {
	0% {
		transform: scaleX(1);
	}
	50% {
		transform: scaleX(1.2);
	}
	100% {
		transform: scaleX(1);
	}
}
.log-out {
	position: relative;
	margin: 5px;
	display: flex;
	margin: 10px;
	align-items: center;
	color: #c2dec1;
	font-weight: 600;
	font-size: 20px;
	justify-content: space-between;
}
.log-out > div {
	display: flex;
	justify-content: center;
	flex-direction: row;
	width: max-content;
	cursor: pointer;
}
.log-out > div:nth-of-type(2) {
	flex-direction: row-reverse;
}
.log-out p {
	margin: 1px 3px;
	font-family: 'Ubuntu', sans-serif;
}
.container {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
}
.bar {
	display: inline-block;
	height: 200px;
	width: 200px;
	border-radius: 50%;
	position: relative;
	border-radius: 50%;
	border-radius: 50%;
	background: linear-gradient(145deg, #1e1c2f, #191728);
	box-shadow: 11px 11px 22px #0d0c15, -11px -11px 22px #2b2843;
	overflow: hidden;
	margin: 10px;
}
.bar:after {
	content: '';
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	height: 80%;
	width: 80%;
	border-radius: 50%;
	border-radius: 50%;

	box-shadow: inset 11px 11px 22px #0d0c15, inset -11px -11px 22px #2b2843;
}
.svgProgressbar {
	width: 100%;
	height: 100%;
	position: relative;
	--doneToday: 100;
}

svg circle {
	position: absolute;
	transform: scale(0.939);
	transform-origin: center;
	fill: none;
	stroke: #d45d44;
	stroke-width: 30px;
	stroke-dasharray: 630px;
	/* stroke-dashoffset: 630px; */
}
.bar:nth-child(1) svg circle {
	animation: bar_1 3s ease forwards;
}
.bar:nth-child(2) svg circle {
	animation: bar_2 1s ease forwards;
}
.bar:nth-child(3) svg circle {
	animation: bar_3 1s ease forwards;
}

/* svg circle {
      stroke-dashoffset: calc(950 - (950*10) / 100);
  } */
@keyframes bar_1 {
	0% {
		stroke-dashoffset: 630px;
	}
	100% {
		stroke-dashoffset: calc(630px - (630px * var(--doneToday)) / 100);
	}
}

.anzeige {
	position: absolute;
	font-size: 20px;
	font-family: 'Montserrat';
	text-align: center;
	top: 50%;
	color: #d45d44;
	left: 50%;
	transform: translate(-50%, -50%);
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1.5fr 0.5fr;
	gap: 0px 0px;
	grid-template-areas:
		'.'
		'.';
}
.anzeige .big {
	margin: auto;
	font-size: 40px;
	color: #d45d44;
	text-shadow: 1px 1px 10px #181726, -1px -1px 10px #201d32;
	text-shadow: 0px 0px 10px rgba(212, 92, 68, 0.1);
	font-weight: 1000;
	margin: 0px auto;
	font-family: 'Patua One', cursive;
}
.anzeige .small {
	text-shadow: 0px 0px 10px rgba(212, 92, 68, 0.1);
	margin: 0px auto;
	font-size: 13px;
	font-weight: 800;
	color: #d45d44;
	font-family: 'Merriweather Sans', sans-serif;
}
.Termine {
	max-width: 320px;
	margin: auto;
	transform-origin: top center;
}
.Termine:nth-child(1) {
	background: red;
}
.todo-item {
}
.b {
	animation: fly-in 400ms ease-in-out;
}
@keyframes fly-in {
	0% {
		transform: scale(0.1) translateY(0%);
	}
	70% {
		transform: scale(1.01) translateY(0%);
	}
	100% {
		transform: scale(1) translateY(0%);
	}
}
.a {
	animation: fly-in2 400ms ease-in-out;
}
@keyframes fly-in2 {
	0% {
		transform: scale(0.1) translateY(0%);
	}
	70% {
		transform: scale(1.01) translateY(0%);
	}
	100% {
		transform: scale(1) translateY(0%);
	}
}
.gridDivider {
	display: block;
}
.extra-space-wrapper {
	padding: 10px;
	margin: 0px;
	border-radius: 15px;
	transition: all 1s;
}
.title-wrapper h1 {
	font-size: 50px;
	font-family: 'Raleway', sans-serif;
	width: 100%;
	margin: auto;
}
@media (max-width: 900px) {
	.extra-space-wrapper {
		transform: scale(1);
		transition: all 1s;
		height: auto;
	}
	.extra-space-wrapper.collapsed {
		transform: scale(0);
		transition: all 1s;
		height: 0;
		/* animation: smoothFadeIn forwards 1s ease-in-out; */
	}
	@keyframes smoothFadeIn {
		0% {
			transform: scale(0);
			height: 0;
		}
		30% {
			transform: scale(0);
			height: auto;
		}
		100% {
			transform: scale(1);
			height: auto;
		}
	}
}
@media (min-width: 900px) {
	.planwrapper {
		display: grid;
		overscroll-behavior: contain;
		grid-template-columns: minmax(10%, 40%) auto 1.2fr;
		grid-template-rows: 1fr;
		gap: 0px 0px;
		grid-template-areas: '. gridDivider .';
		margin-bottom: -20px;
		overflow: hidden;
		max-height: 100vh;
	}
	.title-wrapper {
		position: relative;
		--maskX: 40;
		--maskY: 100;
	}
	.extra-space-wrapper {
		transform: scale(1);
		overflow-y: scroll;
		width: 100%;
		height: 100%;
		padding: auto 40px auto 40px;
		transition: all 100ms;
	}

	.gridDivider {
		grid-area: gridDivider;
	}
	.gridDivider {
		height: 100%;
		margin: 0px 0px;
		background: rgba(158, 10, 10, 0);
		box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
		backdrop-filter: blur(4px);
		-webkit-backdrop-filter: blur(4px);
		border-radius: 10px;
		border: 1px solid rgba(255, 255, 255, 0.18);
	}
	.left-wrapper {
		overflow-y: scroll;
	}
	.week-navigation {
		width: 90%;
		height: 50px;
		margin: 20px auto;
		position: relative;
		display: flex;
		justify-content: space-around;
		flex-direction: row;
		align-items: center;
		border-radius: 20px;
		background: #1c1a2c;
		box-shadow: 11px 11px 22px #181625, -11px -11px 22px #201e33;
		z-index: 0;
		-webkit-tap-highlight-color: transparent;
		max-width: 90%;
	}
	.container {
	}
	.Termine {
		max-width: 100%;
		margin: 20px;
	}
	.pos_ab {
		position: absolute;
		top: 0;
		color: #d45d44;
		right: 0;
		margin: auto;

		transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
		clip-path: polygon(
			0 0,
			calc(var(--maskX) * 1% + (var(--maskY) - 50) * 0.4%) 0,
			calc(var(--maskX) * 1% + (var(--maskY) - 50) * -0.4%) 100%,
			0 100%
		);
	}
}

