	font-weight: 700;
	color: #1c1a2c;
}
.form__div {
	position: relative;
	height: 50px;
}
.form__input {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0px;
	width: 100%;
	height: 100%;
	font-size: 1rem;
	font-size: var(--normal-font-size);
	border: 1px solid #1c1a2c;
	border-radius: 0.5rem;
	color: #1c1a2c;
	outline: none;
	padding: 1rem;
	background: none;
	z-index: 1;
}
.form__label {
	position: absolute;
	left: 1rem;
	top: 1rem;
	padding: 0 0.25rem;
	background-color: #d45d44;
	color: #1c1a2c;
	font-size: 1rem;
	font-size: var(--normal-font-size);
	transition: 0.3s;
}
.form__button {
	display: block;
	margin-left: auto;
	padding: 0.75rem 2rem;
	outline: none;
	border: none;
	background-color: #1c1a2c;
	font-size: 1rem;
	font-size: var(--normal-font-size);
	color: #c2dec1;
	border-radius: 0.5rem;

	transition: 0.3s;
}
.form__button:hover {
	transform: scale(0.97);
}

/*Input focus move up label*/
.form__input:focus + .form__label {
	top: -0.5rem;
	left: 0.8rem;
	color: #1c1a2c;
	font-size: 0.75rem;
	font-size: var(--small-font-size);
	font-weight: 500;
	z-index: 10;
}

/*Input focus sticky top label*/
.form__input:not(:-ms-input-placeholder).form__input:not(:focus) + .form__label {
	top: -0.5rem;
	left: 0.8rem;
	font-size: #c2dec1;
	font-weight: 500;
	z-index: 10;
}
.form__input:not(:placeholder-shown).form__input:not(:focus) + .form__label {
	top: -0.5rem;
	left: 0.8rem;
	font-size: #c2dec1;
	font-weight: 500;
	z-index: 10;
}

/*Input focus*/
.form__input:focus {
	border: 1.5px solid #1c1a2c;
}
