@media (min-width: 900px) {
	.SeeMore {
		display: none;
	}
}
@media (max-width: 900px) {
	.termine-content-divider {
		width: 100%;
		height: 2px;
		background: white;
		position: relative;
		color: #fff;
	}
	.termine-content-divider::before {
		content: 'Stats der Woche';
		position: absolute;
		top: -10px;
		background-color: #1c1a2c;
		padding: 0px 10px;
		left: 50%;
		transform: translateX(-50%);
	}
	.switchStats {
		min-width: 60px;
		height: 30px;
		border: 2px solid #fff;
		z-index: 1000;
		border-radius: 100px;
		height: 30px;
		background: #d45d44;
		position: relative;
	}
	.switchStats.on1 {
		background: rgb(23, 143, 53);
	}
	.small-pop-up {
		position: fixed;
		width: 100px;
		height: 100px;
		background: white;
	}
	.switch-sphere {
		height: 20px;
		width: 20px;

		margin: 3px;
		position: absolute;
		right: 0px;
		border-radius: 100px;
		background: radial-gradient(
			circle at 20% 20%,
			#ffffff,
			rgb(255, 244, 244)
		);
		transition: all 300ms;
	}
	.switch-sphere.on {
		right: 30px !important;
	}
	.chart-card h1 {
		text-align: left;
		margin: 0px 0px 20px 0px;
		color: rgba(255, 255, 255, 1);
		letter-spacing: 0px;
		font-family: 'Ubuntu', sans-serif;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	.SeeMore {
		background: rgba(255, 255, 255, 0.13);
		margin: 20px auto;
		padding: 10px;
		border-radius: 30px;
		max-width: -webkit-max-content;
		max-width: -moz-max-content;
		max-width: max-content;
		color: #fff;
	}
}

.more-details-icon {
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
}

.ChartPopUp {
	position: absolute;
	margin: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	top: 0px;
	-webkit-animation: blurItAll 100ms ease-out forwards;
	        animation: blurItAll 100ms ease-out forwards;
}
@-webkit-keyframes blurItAll {
	from {
	}
	to {
		background: rgba(255, 255, 255, 0);
		box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
		backdrop-filter: blur(5.5px);
		-webkit-backdrop-filter: blur(5.5px);
	}
}
@keyframes blurItAll {
	from {
	}
	to {
		background: rgba(255, 255, 255, 0);
		box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
		backdrop-filter: blur(5.5px);
		-webkit-backdrop-filter: blur(5.5px);
	}
}
.ChartPopUpCard {
	width: 90%;
	background: rgba(255, 255, 255, 0.7);
	box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
	backdrop-filter: blur(5.5px);
	-webkit-backdrop-filter: blur(5.5px);
	border-radius: 10px;

	max-width: 500px;
	position: relative;
	-webkit-animation: popUpChart 100ms ease-in-out forwards;
	        animation: popUpChart 100ms ease-in-out forwards;
	display: grid;
	grid-template-columns: 1.7fr 0.5fr;

	grid-gap: 0px 0px;

	gap: 0px 0px;
	overflow: hidden;
	overflow-y: scroll;
	grid-template-areas:
		'titlePopUp .'
		'textPopUp textPopUp';
	margin: auto;
	position: fixed;
	left: 5%;
	bottom: 10px;
}
.textPopUp {
	grid-area: textPopUp;
	width: 100%;
	text-align: left;
	padding: 10px 22px 20px 22px;
	text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.3);
}
.titlePopUp {
	grid-area: titlePopUp;
	text-align: left;
	margin: 15px auto auto 20px;
	font-size: 30px;
	text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.3);
	font-weight: 800;
}

@-webkit-keyframes popUpChart {
	0% {
		transform: scale(0.3);
	}
	95% {
		transform: scale(1.05);
	}
	100% {
		transform: scale(1);
	}
}

@keyframes popUpChart {
	0% {
		transform: scale(0.3);
	}
	95% {
		transform: scale(1.05);
	}
	100% {
		transform: scale(1);
	}
}
.closingIcon {
	cursor: pointer;
	position: absolute;
	right: 10px;
	top: 10px;
}

