.button-main {
	position: relative;
	width: 170px;
	height: 55px;
	text-align: center;
	line-height: 55px;
	margin: auto;
	text-transform: uppercase;
	background: #4f1964;
	color: #fff;
	font-size: 18px;
	font-weight: 800;
	text-decoration: none;
	letter-spacing: 2px;
	overflow: hidden;
	border-radius: 5px 0px 5px 0px;
	box-shadow: 0px -5px 20px rgba(0, 0, 0, 0.4);
	border-radius: 50px;
	background: #d45d44;
	transition: all 400ms;
}
.button-main span {
	position: relative;
	color: #efefef;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	z-index: 10;
	transition: 0s ease-in;
	margin: 0;
	padding: 0;
	border-radius: 50px;
	background: #d45d44;
	transition: all 400ms;
	cursor: pointer;
}
.button-main:hover {
	transform: scale(1.1);
}
