.Planet {
	position: absolute;
	width: 500px;
	height: 500px;
	right: 0;
	top: 5%;
	animation: Planet 20s ease-in-out infinite;
}
@keyframes Planet {
	0% {
		transform: translateY(0%);
	}
	50% {
		transform: translateY(10%);
	}
	100% {
		transform: translateY(0%);
	}
}
@keyframes Planet2 {
	0% {
