.scroll-to-next-page {
	position: absolute;
	left: 0;
	right: 0;
	bottom: -15px;
	margin-left: auto;
	border-radius: 1000px;
	width: 170px;
	margin-right: auto;
	animation: animation-icon 800ms ease-in-out forwards infinite;
	transition: all 300ms;
}
.scroll-to-back-page {
	position: sticky;
	margin-left: auto;
	margin-bottom: 20px;
	border-radius: 1000px;
	width: 170px;
	margin-right: auto;
	animation: animation-icon 800ms ease-in-out forwards infinite;
	transition: all 300ms;
}
.icon-to-new-page {
	transition: all 500ms;
}
.icon-to-new-page.active {
	transform: rotateY(180deg);
	overflow: hidden;
}
.icon-to-new-pageUp {
	transition: all 500ms;
}
.icon-to-new-pageUp.up {
	transform: rotateY(180deg);
}
@keyframes animation-icon {
	0% {
		transform: translateY(-10%);
	}
	50% {
		transform: translateY(0%);
	}
	100% {
		transform: translateY(-10%);
	}
}
