	opacity: 1;
	z-index: 0;
	overflow: hidden;
	opacity: 0;
	filter: drop-shadow(10px 10px 10px #00000081);
}
.rocket-start {
	animation: rocketS ease-in forwards 2s, fire linear 1.5s forwards;
	position: absolute;
	right: 0;
	left: 0;
	bottom: 10%;
	margin-left: auto;
	margin-right: auto;
	width: 30px;
	color: white;
	max-width: 230px;
	font-family: 'Lato', sans-serif;
	opacity: 1;
	z-index: 9;
	overflow: hidden;
	opacity: 0;
	filter: drop-shadow(10px 10px 10px #00000081);
}
@keyframes rocketS {
	0% {
		opacity: 0;
		transform: translateY(0);
	}
	10% {
		opacity: 100;
	}
	100% {
		transform: translateY(-1000%);
	}
}
@keyframes fire {
	0% {
		filter: drop-shadow(0px 100px 3px rgba(255, 255, 0, 0));
		filter: drop-shadow(0px 100px 5px rgba(255, 0, 0, 0));
	}
	50% {
		filter: drop-shadow(0px 100px 3px rgba(255, 255, 0, 0.115));
		filter: drop-shadow(0px 100px 5px rgba(255, 0, 0, 0.181));
	}
	100% {
		filter: drop-shadow(0px 100px 3px rgba(255, 255, 0, 0.371));
		filter: drop-shadow(0px 100px 6px rgba(255, 0, 0, 0.357));
	}
}
