.Footer {
	display: block;
	background: 'white';
}

.Inside-Footer-Wrapper {
	display: flex;
	justify-content: space-around;
	flex-direction: row;
	max-width: 700px;
	margin: 20px auto;
}
.Copyright {
	margin-bottom: 0px;
	opacity: 0.4;
}
@media (max-width: 518px) {
	.Inside-Footer-Wrapper {
		display: flex;
		justify-content: space-around;
		flex-direction: column;
		max-width: 700px;
		margin: 20px auto;
	}
}

.Inside-Footer-Wrapper a {
	margin: 0;
	padding: 0;
}
